.script {
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: monospace;
  font-size: 1.5rem;
}

.italic {
  font-style: italic;
}

/* NAVIGATION */

.navigation {
  display: flex;
  width: 21rem;
  margin: 5em 0 0;
  justify-content: space-between;
}

.navigation img {
  height: 2em;
  width: 2em;
  opacity: 100%;
}

.navigation img:hover {
  opacity: 70%;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

#li {
  height: 2em;
  width: 2.3em;
}

#twitter {
  height: 2em;
  width: 2em;
}

#twitter:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
